import React, { useEffect, useState } from 'react';
import { toast } from 'react-hot-toast';
import { MovimentoEstoque, CreateMovimentoEstoqueRequest, TipoMovimento } from '@/services/api/movimento-estoque.service';
import { produtoService, Produto } from '@/services/api/produto.service';
import { fornecedorService, Fornecedor } from '@/services/api/fornecedor.service';
import Input from '@/components/form/input/InputField';
import Select from '@/components/ui/Select';
import Button from '@/components/ui/Button';
import DatePicker from '@/components/form/date-picker';
import { formatCurrency, formatCurrencyInput, parseCurrencyInput } from '@/utils/format';

type FormErrors = {
  produtoId?: string;
  fornecedorId?: string;
  tipo?: string;
  quantidade?: string;
  valorUnitario?: string;
  data?: string;
};

type MovimentacaoFormProps = {
  movimentacao?: MovimentoEstoque | null;
  onSubmit: (data: CreateMovimentoEstoqueRequest) => Promise<void>;
  onCancel: () => void;
};

const tipoOptions = [
  { value: 'ENTRADA', label: 'Entrada' },
  { value: 'SAIDA', label: 'Saída' }
];

const MovimentacaoForm: React.FC<MovimentacaoFormProps> = ({
  movimentacao,
  onSubmit,
  onCancel
}) => {
  const [formData, setFormData] = useState<CreateMovimentoEstoqueRequest>({
    tipo: movimentacao?.tipo || 'ENTRADA',
    quantidade: movimentacao?.quantidade || 1,
    data: movimentacao?.data || new Date(),
    produtoId: movimentacao?.produtoId || 0,
    valorUnitario: movimentacao?.valorUnitario || 0,
    fornecedorId: movimentacao?.fornecedorId || 0,
    observacoes: movimentacao?.observacoes || ''
  });
  const [valorFormatado, setValorFormatado] = useState(
    movimentacao?.valorUnitario ? formatCurrency(movimentacao.valorUnitario).replace('R$', '').trim() : ''
  );

  const [produtos, setProdutos] = useState<Produto[]>([]);
  const [fornecedores, setFornecedores] = useState<Fornecedor[]>([]);
  const [isLoadingProdutos, setIsLoadingProdutos] = useState(true);
  const [isLoadingFornecedores, setIsLoadingFornecedores] = useState(true);
  const [errors, setErrors] = useState<FormErrors>({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    loadProdutos();
    loadFornecedores();
  }, []);

  const loadProdutos = async () => {
    try {
      setIsLoadingProdutos(true);
      const response = await produtoService.list({ page: 1, limit: 100 });
      setProdutos(response.data);
    } catch (error) {
      toast.error('Erro ao carregar produtos');
    } finally {
      setIsLoadingProdutos(false);
    }
  };

  const loadFornecedores = async () => {
    try {
      setIsLoadingFornecedores(true);
      const response = await fornecedorService.list({ page: 1, perPage: 100 });
      setFornecedores(response.data);
    } catch (error) {
      toast.error('Erro ao carregar fornecedores');
    } finally {
      setIsLoadingFornecedores(false);
    }
  };

  const validateForm = () => {
    const newErrors: FormErrors = {};
    const required = ['produtoId', 'fornecedorId', 'tipo', 'quantidade', 'valorUnitario', 'data'];

    required.forEach(field => {
      if (!formData[field as keyof typeof formData]) {
        newErrors[field as keyof FormErrors] = `${field.charAt(0).toUpperCase() + field.slice(1)} é obrigatório`;
      }
    });

    if (formData.quantidade < 1) {
      newErrors.quantidade = 'Quantidade deve ser maior que zero';
    }

    if (formData.valorUnitario < 0) {
      newErrors.valorUnitario = 'Valor unitário não pode ser negativo';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!validateForm()) return;

    setIsSubmitting(true);
    try {
      await onSubmit(formData);
    } catch (error) {
      toast.error('Erro ao salvar movimentação');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div>
          <label htmlFor="tipo" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Tipo de Movimentação
          </label>
          <Select
            id="tipo"
            name="tipo"
            value={tipoOptions.find(opt => opt.value === formData.tipo)}
            options={tipoOptions}
            onChange={(option) => setFormData({
              ...formData,
              tipo: (option as { value: TipoMovimento }).value
            })}
            placeholder="Selecione o tipo"
            error={errors.tipo}
            className="w-full"
          />
        </div>

        <div>
          <label htmlFor="produtoId" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Produto
          </label>
          <Select
            id="produtoId"
            name="produtoId"
            isLoading={isLoadingProdutos}
            value={produtos
              .filter(p => p.id === formData.produtoId)
              .map(p => ({ value: String(p.id), label: p.nome }))[0]
            }
            options={produtos.map(p => ({
              value: String(p.id),
              label: p.nome + ` (${p.estoque} em estoque)`
            }))}
            onChange={(option) => {
              if (option) {
                setFormData({
                  ...formData,
                  produtoId: parseInt((option as { value: string }).value, 10)
                });
              }
            }}
            placeholder="Selecione o produto"
            error={errors.produtoId}
            className="w-full"
          />
        </div>

        <div>
          <label htmlFor="quantidade" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Quantidade
          </label>
          <Input
            id="quantidade"
            name="quantidade"
            type="number"
            min="1"
            value={formData.quantidade}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => setFormData({
              ...formData,
              quantidade: parseInt(e.target.value) || 0
            })}
            error={errors.quantidade}
          />
        </div>

        <div>
          <label htmlFor="fornecedorId" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Fornecedor
          </label>
          <Select
            id="fornecedorId"
            name="fornecedorId"
            isLoading={isLoadingFornecedores}
            value={fornecedores
              .filter(f => f.id === formData.fornecedorId)
              .map(f => ({ value: String(f.id), label: f.nome }))[0]
            }
            options={fornecedores.map(f => ({
              value: String(f.id),
              label: f.nome
            }))}
            onChange={(option) => {
              if (option) {
                setFormData({
                  ...formData,
                  fornecedorId: parseInt((option as { value: string }).value, 10)
                });
              }
            }}
            placeholder="Selecione o fornecedor"
            error={errors.fornecedorId}
            className="w-full"
          />
        </div>

        <div>
          <label htmlFor="valorUnitario" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Valor Unitário
          </label>
          <div className="relative">
            <span className="absolute z-1 left-3 top-1/2 -translate-y-1/2 text-gray-500 dark:text-gray-300">
              R$
            </span>
            <Input
              id="valorUnitario"
              name="valorUnitario"
              type="text"
              value={valorFormatado}
              className="pl-9"
              onChange={(e) => {
                const input = e.target.value.replace(/\D/g, '');
                if (input === '') {
                  setValorFormatado('');
                  setFormData({ ...formData, valorUnitario: 0 });
                  return;
                }

                const formatted = formatCurrencyInput(input);
                setValorFormatado(formatted);
                setFormData({ ...formData, valorUnitario: parseCurrencyInput(input) });
              }}
              // onChange={(e: React.ChangeEvent<HTMLInputElement>) => setFormData({
              //   ...formData,
              //   valorUnitario: parseFloat(e.target.value) || 0
              // })}
              error={errors.valorUnitario}
            />
          </div>
        </div>

        <div>
          <label htmlFor="data" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Data
          </label>
          <DatePicker
            id="data"
            onChange={(selectedDates: Date[]) => {
              if (selectedDates[0]) {
                setFormData({
                  ...formData,
                  data: selectedDates[0]
                });
              }
            }}
            defaultDate={new Date(formData.data)}
          />
          {errors.data && (
            <p className="mt-1 text-sm text-red-600">{errors.data}</p>
          )}
        </div>
      </div>

      <div>
        <label htmlFor="observacoes" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
          Observações
        </label>
        <textarea
          id="observacoes"
          name="observacoes"
          rows={3}
          value={formData.observacoes}
          onChange={(e) => setFormData({ ...formData, observacoes: e.target.value })}
          className="w-full rounded-lg border border-stroke bg-transparent px-4 py-3 outline-none focus:border-primary focus-visible:shadow-none dark:border-gray-600 dark:bg-gray-900 dark:focus:border-primary"
        />
      </div>

      <div className="flex justify-end space-x-4 pt-4">
        <Button
          variant="secondary"
          onClick={onCancel}
          disabled={isSubmitting}
        >
          Cancelar
        </Button>
        <Button
          variant="primary"
          type="submit"
          disabled={isSubmitting}
        >
          {isSubmitting ? 'Salvando...' : movimentacao ? 'Atualizar' : 'Criar'}
        </Button>
      </div>
    </form>
  );
};

export default MovimentacaoForm;
