import React from 'react';
import { formatDate, formatCurrency } from '@/utils/format';
import { MovimentoEstoque } from '@/services/api/movimento-estoque.service';
import { Fornecedor } from '@/services/api/fornecedor.service';
import Badge from '../ui/badge/Badge';
import ButtonCard from '../ui/button/ButtonCard';

type MovimentacaoCardProps = {
  movimentacao: MovimentoEstoque;
  onEdit: () => void;
  onDelete: () => void;
};

const tipoColors = {
  ENTRADA: 'success',
  SAIDA: 'error'
} as const;

const MovimentacaoCard: React.FC<MovimentacaoCardProps> = ({
  movimentacao,
  onEdit,
  onDelete
}) => {
  return (
    <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm hover:shadow-md shadow-gray-200 dark:shadow-gray-700 border border-gray-200 dark:border-gray-700 p-4">
      <div>
        <div className="flex justify-between items-start mb-3">
          <div>
            <div className="flex items-center gap-2 flex-wrap overflow-x-hidden">
              <h3 className="font-medium text-gray-900 dark:text-white">
                {movimentacao.produto?.nome || 'Produto não encontrado'} - {formatCurrency(movimentacao.valorUnitario)}
              </h3>
              <Badge
                color={tipoColors[movimentacao.tipo]}
                className="capitalize"
              >
                {movimentacao.tipo.toLowerCase()}
              </Badge>
            </div>
            <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
              Quantidade: {movimentacao.quantidade}
            </p>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Data: {formatDate(movimentacao.data)}
            </p>
            {movimentacao.fornecedor && (
              <p className="text-sm text-gray-500 dark:text-gray-400">
                Fornecedor: {movimentacao.fornecedor.nome}
              </p>
            )}
          </div>
          <div className="flex gap-2">
            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onEdit}
              title="Editar"
              icon="edit"
            />
            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onDelete}
              title="Excluir"
              icon="delete"
            />
          </div>
        </div>

        <div className="space-y-2">
          {movimentacao.observacoes && (
            <p className="text-sm text-gray-600 dark:text-gray-300 line-clamp-2">
              {movimentacao.observacoes}
            </p>
          )}
        </div>
      </div>
    </div>
  );
};

export default MovimentacaoCard;
