"use client";

import React, { useEffect, useState, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { toast } from 'react-hot-toast';
import Button from '@/components/ui/Button';
import { Modal } from '@/components/ui/modal';
import Input from '@/components/form/input/InputField';
import Select from '@/components/ui/Select';
import { MovimentoEstoque } from '@/services/api/movimento-estoque.service';
import { movimentoEstoqueService } from '@/services/api/movimento-estoque.service';
import MovimentacaoCard from '@/components/movimentacao/MovimentacaoCard';
import Pagination from '@/components/ui/Pagination';
import MovimentacaoForm from '@/components/movimentacao/MovimentacaoForm';
import Label from '@/components/form/Label';

type FilterOptions = {
  tipo?: 'ENTRADA' | 'SAIDA';
  produtoIds?: number[];
  orderBy?: 'data_asc' | 'data_desc' | 'quantidade_asc' | 'quantidade_desc';
};

export default function MovimentacoesPage() {
  const router = useRouter();
  const [movimentacoes, setMovimentacoes] = useState<MovimentoEstoque[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedMovimentacao, setSelectedMovimentacao] = useState<MovimentoEstoque | null>(null);
  const [filters, setFilters] = useState<FilterOptions>({});
  const [itemsPerPage, setItemsPerPage] = useState(9);

  const loadMovimentacoes = useCallback(async () => {
    try {
      setLoading(true);
      const response = await movimentoEstoqueService.list({
        page,
        perPage: itemsPerPage,
        search: searchTerm,
        tipo: filters.tipo,
        produtoIds: filters.produtoIds,
        orderBy: filters.orderBy
      });
      setMovimentacoes(response.data);
      setTotalPages(response.pagination.pages);
    } catch (error) {
      toast.error('Erro ao carregar movimentações');
    } finally {
      setLoading(false);
    }
  }, [page, searchTerm, filters, itemsPerPage]);

  useEffect(() => {
    loadMovimentacoes();
  }, [loadMovimentacoes]);

  const handleSearch = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(event.target.value);
    setPage(1); // Resetar para primeira página ao pesquisar
  };

  const handleFilterChange = (newFilters: Partial<FilterOptions>) => {
    setFilters(prev => ({ ...prev, ...newFilters }));
    setPage(1); // Resetar para primeira página ao filtrar
  };

  const handleEdit = (movimentacao: MovimentoEstoque) => {
    setSelectedMovimentacao(movimentacao);
    setIsModalOpen(true);
  };

  const handleDelete = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja excluir esta movimentação?')) return;

    try {
      await movimentoEstoqueService.delete(id);
      toast.success('Movimentação excluída com sucesso');
      loadMovimentacoes();
    } catch (error) {
      toast.error('Erro ao excluir movimentação');
    }
  };

  const handleSave = async (data: any) => {
    try {
      if (selectedMovimentacao) {
        await movimentoEstoqueService.update(selectedMovimentacao.id, data);
        toast.success('Movimentação atualizada com sucesso');
      } else {
        await movimentoEstoqueService.create(data);
        toast.success('Movimentação criada com sucesso');
      }
      setIsModalOpen(false);
      setSelectedMovimentacao(null);
      loadMovimentacoes();
    } catch (error) {
      toast.error('Erro ao salvar movimentação');
    }
  };

  const tipoOptions = [
    { value: 'ENTRADA', label: 'Entrada' },
    { value: 'SAIDA', label: 'Saída' }
  ];

  const orderByOptions = [
    { value: 'data_desc', label: 'Data - Mais recente' },
    { value: 'data_asc', label: 'Data - Mais antiga' },
    { value: 'quantidade_desc', label: 'Quantidade - Maior' },
    { value: 'quantidade_asc', label: 'Quantidade - Menor' }
  ];

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white mb-2">
              Movimentações de Estoque
            </h1>
            <p className="text-sm text-gray-500">
              Gerencie todas as entradas e saídas de produtos no estoque
            </p>
          </div>
          <div className="flex items-center gap-2">
            <Button
              variant="primary"
              onClick={() => {
                setSelectedMovimentacao(null);
                setIsModalOpen(true);
              }}
            >
              Nova Movimentação
            </Button>
          </div>
        </div>

        <div className="mt-6 flex flex-col sm:flex-row items-center gap-4">
          <div className="flex-1 w-full sm:max-w-xs">
            <Input
              type="text"
              placeholder="Buscar movimentações..."
              value={searchTerm}
              onChange={handleSearch}
              className="w-full"
            />
          </div>
          <div className="flex flex-wrap gap-3">
            <Select
              placeholder="Tipo"
              options={tipoOptions}
              value={tipoOptions.find(opt => opt.value === filters.tipo)}
              onChange={(option) => handleFilterChange({ 
                tipo: option ? (option as { value: 'ENTRADA' | 'SAIDA' }).value : undefined 
              })}
              isClearable
              className="whitespace-nowrap min-w-40"
            />
            <Select
              placeholder="Ordenar por"
              options={orderByOptions}
              value={orderByOptions.find(opt => opt.value === filters.orderBy)}
              onChange={(option) => handleFilterChange({ 
                orderBy: option ? (option as any).value : undefined 
              })}
              isClearable
              className="whitespace-nowrap min-w-50"
            />
          </div>
        </div>
      </div>

        <div className="flex justify-between mb-4 gap-2 flex-wrap">
          <div className='flex items-center gap-0.5'>
            <Label className='flex items-center mb-0 h-full'>Quantidade por página</Label>
            <Input
              className='max-w-24'
              defaultValue={itemsPerPage}
              max={100}
              min={1}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                const newValue = Number(e.target.value) || 9;
                setItemsPerPage(newValue);
                setPage(1);
              }}
            />
          </div>
            <Pagination
              currentPage={page}
              totalPages={totalPages}
              onPageChange={setPage}
            />
        </div>

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {movimentacoes.length > 0 ? (
            movimentacoes.map((movimentacao) => (
              <MovimentacaoCard
                key={movimentacao.id}
                movimentacao={movimentacao}
                onEdit={() => handleEdit(movimentacao)}
                onDelete={() => handleDelete(movimentacao.id)}
              />
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhuma movimentação encontrada
              </p>
            </div>
          )}
        </div>
      )}

          {totalPages > 1 && (
      <div className="mt-6">
        <div className="flex justify-end mb-4 gap-2 flex-wrap">
            <Pagination
              currentPage={page}
              totalPages={totalPages}
              onPageChange={setPage}
            />
        </div>
      </div>
          )}

      <Modal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedMovimentacao(null);
        }}
        className="max-w-[800px] m-4"
      >
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              {selectedMovimentacao ? 'Editar Movimentação' : 'Novo Movimentação'}
            </h4>
            <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
              {selectedMovimentacao 
                ? 'Atualize as informações do movimentação.' 
                : 'Preencha os dados para criar um novo movimentação.'
              }
            </p>
          </div>
          <MovimentacaoForm
            movimentacao={selectedMovimentacao}
            onSubmit={handleSave}
            onCancel={() => {
              setIsModalOpen(false);
              setSelectedMovimentacao(null);
            }}
          />
        </div>
      </Modal>
    </div>
  );
}
